import React, {useState} from 'react';
import './form.css';
import Checkmark from "./checkmark";
import Ali from "./foto all MT/Ali.jpeg";
import Elsa from "./foto all MT/Elsa.jpeg";
import Sahala from "./foto all MT/Sahala.jpeg";

const nominatorsImg = [Ali, Elsa, Sahala];
const nominatorsName = ['Ali', 'Elsa', 'Sahala'];

const Form = React.memo(({handleClick, fav}) => {

	const [state, setState] = useState('form__entering');
	const [showError, setShowError] = useState(false);
	const [favorit, setFavorit] = useState(fav);

	const checkNominator = (me, favorit) => {
		if(favorit) {
			if(me === favorit) return true;
			return false;
		}
		return true;
	}

	setTimeout(() => {
		if( state === 'form__entering') setState('form');
	}, 200);

	const buttonClicked = (e, nominator) => {
		setShowError(false);
		let component = e.target;
		if(component.nodeName === "IMG" || component.nodeName === 'DIV') {
			component = component.parentNode;
		}
		if(component.nodeName === "svg") {
			component = component.parentNode.parentNode;
		}

		component.classList.add('nominator-button-clicked');
		setTimeout(() => {
			component.classList.remove('nominator-button-clicked');
		}, 100);
		setTimeout(() => {
			component.classList.add('nominator-button-clicked');
		}, 200);
		setTimeout(() => {
			component.classList.remove('nominator-button-clicked');
			if(!favorit) {setFavorit(nominator) ; return}
			if(favorit === nominator) {setFavorit(null) ; return}
		}, 300);
	}

	const handleSubmit = (e) => {
		e.target.classList.add('submit-button-clicked');
		setTimeout(() => {
			e.target.classList.remove('submit-button-clicked');
		}, 100);
		setTimeout(() => {
			e.target.classList.add('submit-button-clicked');
		}, 200);
		setTimeout(() => {
			e.target.classList.remove('submit-button-clicked');
			if(favorit) {
				setState('form__exiting');
			} else {
				setShowError(true);
			}
		}, 300);
		setTimeout(() => {
			if(favorit) {
				handleClick(favorit);
			}
		},350)
	}

	  return (
		   <div className={`${state}`}>
			   	<h3 className="form__title">Pilih 1 orang staff Brain Academy Pontianak favorit kamu selama di Brain Academy?</h3>
			   	<h4 className="form__description">jawabanmu bersifat anonim</h4>
		   	<div className="nominators__wrapper">
		   		{nominatorsImg.map( (nominator, index) => {
		   			return <button 
		   						key={nominatorsName[index]} 
		   						className={`nominator__button ${checkNominator(nominatorsName[index], favorit) ? null : 'nominator__button-dark'}`} 
		   						onClick={(e) => buttonClicked(e, nominatorsName[index])} 
		   						>
			   						<img className={`nominator__img ${checkNominator(nominatorsName[index], favorit) ? null : 'nominator__img-dark'}`}  src={nominator} alt=""/>
			   						<div className={`nominator__text ${checkNominator(nominatorsName[index], favorit) ? null : 'nominator__text-dark'}`} >{`${index === 0 ? 'bang' : 'kak'} ${nominatorsName[index]}`}</div>
			   						{favorit === nominatorsName[index] ? <Checkmark/> : null}
		   					</button>
		   		})}
		   	</div>
		   	<div>
		   		{showError 
		   			? <div className='error-text-wrapper'>
		   				<p className='error-text'>Harus pilih 1 staff terfavorit</p>
		   			  </div> 
		   			: null
		   		}
		   		
		   		<button className='form__submit-button' onClick={handleSubmit}>Submit</button>
		   	</div>
		   </div>
	  )
});

export default Form;